import React from 'react';
import './Button.css';

const STYLES = ['btn--primary', 'btn--outline', 'btn--parks'];
const SIZES = ['btn--medium', 'btn--large'];

export const SanLuisDirectionsButton = ({
  children,
  type,
  onClick,
  buttonStyle,
  buttonSize,
}) => {
  const checkButtonStyle = STYLES.includes(buttonStyle)
    ? buttonStyle
    : STYLES[0];

  const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

  return (
    <a
      href='https://www.google.com/maps/place/San+Luis+Obispo+Skate+Park,+1050+Oak+St,+San+Luis+Obispo,+CA+93405,+USA/@35.2900529,-120.666352,17z/data=!3m1!4b1!4m5!3m4!1s0x80ecf1ab134f4ff5:0xb2465e8bbfb9519e!8m2!3d35.2900576!4d-120.6641398'
      target='_blank'
      className='btn-mobile'
      rel='noreferrer'
    >
      <button
        className={`btn ${checkButtonStyle} ${checkButtonSize}`}
        onClick={onClick}
        type={type}
      >
        {children}
      </button>
    </a>
  );
};
