import React from 'react';
import '../../App.css';
import AboutComp from '../AboutComp';
import Footer from '../Footer';

export default function AboutUs() {
  return (
    <>
      <div className='profile-container'>
        <AboutComp
          description='Hey there! Thank you for checking out my website! I am a junior web developer looking to break into the industry. My main focus is making web apps using reactjs. I will have
all my projects published to my github as well as my personal portfolio site which you can access with the buttons below! Feel free to shoot me an email if you would like to
get in contact with me, I am always looking to meet more people within the web development community!
'
        />
      </div>
      <Footer />
    </>
  );
}
