import './App.css';
import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Navbar from './components/Navbar';
import Home from './components/pages/home';
import SignUp from './components/pages/SignUp';
import Parks from './components/pages/Parks';
import AboutUs from './components/pages/AboutUs';
import VeniceBeach from './components/pages/VenicePark';
import BanffPark from './components/pages/Banff';
import VansPark from './components/pages/VansPark';
import LakeCunningham from './components/pages/LakeCunningham';
import Petruiss from './components/pages/Petruiss';
import LindaVista from './components/pages/LindaVista';
import SanLuis from './components/pages/SanLuis';

function App() {
  return (
    <div>
      <Router>
        <Navbar />
        <Switch>
          <Route path='/' exact component={Home} />
          <Route path='/parks' component={Parks} />
          <Route path='/about-us' component={AboutUs} />
          <Route path='/sign-up' component={SignUp} />
          <Route path='/venice-beach' component={VeniceBeach} />
          <Route path='/banff' component={BanffPark} />
          <Route path='/vans-park' component={VansPark} />
          <Route path='/lake-cunningham' component={LakeCunningham} />
          <Route path='/peitruss' component={Petruiss} />
          <Route path='/linda-vista' component={LindaVista} />
          <Route path='/san-luis' component={SanLuis} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
