import React from 'react';
import './Button.css';

const STYLES = ['btn--primary', 'btn--outline', 'btn--parks'];
const SIZES = ['btn--medium', 'btn--large'];

export const BanffDirectionsButton = ({
  children,
  type,
  onClick,
  buttonStyle,
  buttonSize,
}) => {
  const checkButtonStyle = STYLES.includes(buttonStyle)
    ? buttonStyle
    : STYLES[0];

  const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

  return (
    <a href='https://www.google.ca/maps/place/Banff+Skateboard+Park/@51.1737513,-115.5834514,16z/data=!4m9!1m2!2m1!1sbanff+skatepark!3m5!1s0x5370ca41c0ca05f3:0xf78e01ea1092d901!8m2!3d51.1737513!4d-115.579074!15sCg9iYW5mZiBza2F0ZXBhcmtaESIPYmFuZmYgc2thdGVwYXJrkgEPc2thdGVib2FyZF9wYXJrmgEkQ2hkRFNVaE5NRzluUzBWSlEwRm5TVU5ETkdKdFJtcG5SUkFC' target='_blank'  className='btn-mobile' rel="noreferrer" >
      <button
        className={`btn ${checkButtonStyle} ${checkButtonSize}`}
        onClick={onClick}
        type={type}
      >
        {children}
      </button>
    </a>
  );
};
