import React from 'react';
import './Button.css';

const STYLES = ['btn--primary', 'btn--outline', 'btn--parks'];
const SIZES = ['btn--medium', 'btn--large'];

export const PeitrussDirectionsButton = ({
  children,
  type,
  onClick,
  buttonStyle,
  buttonSize,
}) => {
  const checkButtonStyle = STYLES.includes(buttonStyle)
    ? buttonStyle
    : STYLES[0];

  const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

  return (
    <a
      href='https://www.google.ca/maps/place/Skatepark+Péitruss/@49.6066283,6.1307078,17z/data=!3m1!4b1!4m5!3m4!1s0x479548d216e987a1:0x9893af5f5b40f992!8m2!3d49.6066248!4d6.1329018'
      target='_blank'
      className='btn-mobile'
      rel='noreferrer'
    >
      <button
        className={`btn ${checkButtonStyle} ${checkButtonSize}`}
        onClick={onClick}
        type={type}
      >
        {children}
      </button>
    </a>
  );
};
