import React from 'react';
import './Button.css';

const STYLES = ['btn--primary', 'btn--outline', 'btn--parks'];
const SIZES = ['btn--medium', 'btn--large'];

export const VeniceBeachDirectionsButton = ({
  children,
  type,
  onClick,
  buttonStyle,
  buttonSize,
}) => {
  const checkButtonStyle = STYLES.includes(buttonStyle)
    ? buttonStyle
    : STYLES[0];

  const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

  return (
    <a href='https://www.google.ca/maps/place/Venice+Beach+Skatepark./@33.98706,-118.4777139,17z/data=!3m1!4b1!4m5!3m4!1s0x80c2bab79cb7dde5:0x635f1fdc6f199ffe!8m2!3d33.98706!4d-118.4755252' target='_blank'  className='btn-mobile' rel="noreferrer" >
      <button
        className={`btn ${checkButtonStyle} ${checkButtonSize}`}
        onClick={onClick}
        type={type}
      >
        {children}
      </button>
    </a>
  );
};
