import React from 'react';
import './Button.css';

const STYLES = ['btn--primary', 'btn--outline', 'btn--parks'];
const SIZES = ['btn--medium', 'btn--large'];

export const VansBeachDirectionsButton = ({
  children,
  type,
  onClick,
  buttonStyle,
  buttonSize,
}) => {
  const checkButtonStyle = STYLES.includes(buttonStyle)
    ? buttonStyle
    : STYLES[0];

  const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

  return (
    <a href='https://www.google.ca/maps/place/Vans+Off+The+Wall+Skatepark/@33.7350373,-118.0005543,17z/data=!3m1!4b1!4m5!3m4!1s0x80dd266bcd690319:0xf094c5d68908f67a!8m2!3d33.7350329!4d-117.9983603' target='_blank'  className='btn-mobile' rel="noreferrer" >
      <button
        className={`btn ${checkButtonStyle} ${checkButtonSize}`}
        onClick={onClick}
        type={type}
      >
        {children}
      </button>
    </a>
  );
};
