import React from 'react';
import CardItem from './CardItem';
import './Cards.css';

function ParksCards() {
  return (
    <div className='cards'>
      <h1>Check out these EPIC parks</h1>
      <div className='cards__container'>
        <div className='cards__wrapper'>
          <ul className='cards__items'>
            <CardItem
              src='images/venice-beach/venice-beach-skatepark.jpg'
              text='Skate beside the ocean in Venice Beach, California'
              label='Venice Beach'
              path='/venice-beach'
            />
            <CardItem
              src='images/banff/Banff.jpg'
              text='Skate amongst the rocky mountains in Banff, Alberta'
              label='Banff'
              path='/banff'
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src='images/vans-park/Vans-skatepark.jpg'
              text='Skate on the famous Vans Park in Huntington Beach, California'
              label='Vans Park'
              path='/vans-park'
            />
            <CardItem
              src='images/lake-cunningham/lakecunningham.jpg'
              text='Skate one of the worlds best parks in Lake Cunningham, Nebraska'
              label='Lake Cunningham'
              path='/lake-cunningham'
            />
            <CardItem
              src='images/peitruss/Peitruss.jpg'
              text='Skate with an incredible view in Luxembourg City'
              label='Péitruss'
              path='/peitruss'
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src='images/linda-vista/linda-vista.jpg'
              text='Skatepark with amazing flow in Linda Vista, California'
              label='Linda Vista'
              path='/linda-vista'
            />
            <CardItem
              src='images/san-luis/slo.webp'
              text='Skate this other-wordly wave in San Luis Obispo, California'
              label='San Luis Obispo'
              path='/san-luis'
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default ParksCards;
