import React from 'react';
import './pages/park-page.css';

function ParkItem(props) {
  return (
    <div className='article-wrapper'>
      <div className='article-content'>
        <h1 className='article-title'>Park Features</h1>
        <p className='article-description'>{props.description}</p>
        <img
          className='article-pictures img-1'
          alt={props.alt}
          src={props.src1}
        />
        <img
          className='article-pictures img-2'
          alt={props.alt}
          src={props.src2}
        />
        <img
          className='article-pictures img-3'
          alt={props.alt}
          src={props.src3}
        />
        <img
          className='article-pictures img-4'
          alt={props.alt}
          src={props.src4}
        />
      </div>
    </div>
  );
}

export default ParkItem;
