import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import '../../App.css';
require('dotenv').config();

const AnyReactComponent = ({ text }) => <div>{text}</div>;

class MapApi6 extends Component {
  static defaultProps = {
    center: {
      lat: 32.79254629056142, 
      lng: -117.17029318652202,
    },
    zoom: 17,
  };

  render() {
    return (
      <div className='Map'>
        <GoogleMapReact
          bootstrapURLKeys={{ key: process.env.REACT_APP_API_KEY }}
          defaultCenter={this.props.center}
          defaultZoom={this.props.zoom}
        >
          <AnyReactComponent
            lat={this.center}
            lng={this.center}
            text='Linda Vista Skatepark'
          />
        </GoogleMapReact>
      </div>
    );
  }
}

export default MapApi6;
