import React from 'react';
import './AboutComp.css';
import '../App.css';

function AboutComp(props) {
  return (
    <div className='profile-wrapper'>
      <div className='profile-content'>
        <h1 className='profile-title'>Redline 7k</h1>
        <h2 className='profile-dev'>Junior Software Developer</h2>
        <p className='profile-description'>{props.description}</p>
        <a className='profile-email' href=' mailto: mully780@gmail.com'>
          E-mail me here!
        </a>
        <a className='github' href='https://mully780.github.io'>
          Github Projects
        </a>
      </div>
    </div>
  );
}

export default AboutComp;
