import React from 'react';
import './Button.css';

const STYLES = ['btn--primary', 'btn--outline', 'btn--parks'];
const SIZES = ['btn--medium', 'btn--large'];

export const LakeCunninghamDirectionsButton = ({
  children,
  type,
  onClick,
  buttonStyle,
  buttonSize,
}) => {
  const checkButtonStyle = STYLES.includes(buttonStyle)
    ? buttonStyle
    : STYLES[0];

  const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0];

  return (
    <a href='https://www.google.ca/maps/place/Lake+Cunningham+Action+Sports+Park/@37.3361522,-121.805452,17z/data=!3m1!4b1!4m5!3m4!1s0x808e2d5a743c2b99:0x2fedc9f8cb05f9f6!8m2!3d37.336148!4d-121.803258' target='_blank'  className='btn-mobile' rel="noreferrer" >
      <button
        className={`btn ${checkButtonStyle} ${checkButtonSize}`}
        onClick={onClick}
        type={type}
      >
        {children}
      </button>
    </a>
  );
};
